.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text-center {
  text-align: center;
}

.nav {
  position: fixed;
  top: 0px;
}

.footer {
  background-color: #282c34;
  color: white;
  margin: 0;
}

.fullscreenItem {
  height: 100vh;
  width: 100%;
}

.fullscreenContent {
  margin-top: 25vh;
  color: grey;
}

.aboutPage {
  width: 100%;
  height: 100%;
}

.profilePic {
  width: 260px;
  height: 210px;
  margin: 20px;
  border-radius: 5%;
}

p {
  font-size: 1rem;
}

.educationAccordion {
  width: 70%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  color: grey;

  .educationInfoGroup {
    text-align: left;

    .titleRow {
      font-size: 1.5rem;
    }

    .schoolRow {
      font-size: 1.25rem;
    }

    .courseRow {
      p {
        font-size: 1.25rem;
      }
      .courseList {
        font-size: 1rem;

        li {
          display: inline-block;
          margin: 5px;
          padding: 5px;
          border: lightblue 1px solid;
          border-radius: 5px;
        }
      }
    }
  }
}

.workPage {
  margin-top: 15vh;
  
  .jumbotron {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */

  .fullscreenItem {
    height: auto;
    width: 90%;
  }

  .fullscreenContent {
    margin-top: 40px;
    height: auto;

    &:first-of-type {
      margin-top: 80px;
    }

    .jumbotron {
      height: auto;
    }
  }

  ul.courseList {
    padding-left: 0%;

    li {
      text-align: center;
      width: 100%;
    }
  }
}

